<template>
  <div>
    <div class="cart">
      <div class="content" @click="toggleList">
        <div class="content-left">
          <div class="logo-wrapper">
            <div class="logo" :class="{'on': totalCount > 0}">
              <i class="icon-shopping_cart" :class="{'on': totalCount > 0}"></i>
            </div>
            <div class="num" v-show="totalCount > 0">{{ totalCount }}</div>
          </div>
          <div class="price" :class="{'on': totalPrice > 0}">￥{{ totalPrice }}</div>
          <div class="desc">另需配送费￥{{ deliveryPrice }}</div>
        </div>
        <div class="content-right" @click.stop.prevent="pay"> <!--阻止点击事件冒泡-->
          <div class="pay" :class="payClass">{{ payDesc }}</div>
        </div>
      </div>
      <div class="ball-container">
        <div v-for="(ball, index) in balls" :key="index">
          <transition name="drop" @before-enter="beforeDrop" @enter="dropping" @after-enter="afterDrop">
            <div v-show="ball.show" class="ball">
              <div class="inner inner-hook" ref="inner"></div>
            </div>
          </transition>
        </div>
      </div>
      <transition name="fold">
        <div class="cart-list" v-show="listShow">
          <div class="list-header">
            <h1 class="title">购物车</h1>
            <span class="empty" @click="empty">清空</span>
          </div>
          <div class="list-content" ref="listContent">
            <ul>
              <li class="food" v-for="(food, index) in selectFoods" :key="index">
                <span class="name">{{ food.name }}</span>
                <div class="price">
                  <span>￥{{ food.price*food.count }}</span>
                </div>
                <div class="cartcontrol-wrapper">
                  <cartcontrol @increase="increaseFood" :food="food"></cartcontrol>
                </div>
              </li>
            </ul>
          </div>
        </div>
      </transition>
    </div>
    <transition name="fade">
      <div class="list-mask" @click="hideList" v-show="listShow"></div>
    </transition>
  </div>
</template>

<script>

import cartcontrol from '@/components/cartcontrol/cartcontrol'
import BScroll from 'better-scroll'

export default {
  name: 'cart',
  props: {
    selectFoods: {
      type: Array,
      default () {
        return []
      }
    },
    deliveryPrice: {
      type: Number,
      default: 0
    },
    minPrice: {
      type: Number,
      default: 0
    }
  },
  data () {
    return {
      balls: [ // 小球动画
        {
          show: false
        },
        {
          show: false
        }
      ],
      dropBalls: [],
      fold: true // 购物车默认折叠状态
    }
  },
  computed: {
    totalPrice () {
      let total = 0
      this.selectFoods.forEach((food) => {
        total += food.price * food.count
      })
      return total
    },
    totalCount () {
      let count = 0
      this.selectFoods.forEach((food) => {
        count += food.count
      })
      return count
    },
    payDesc () {
      if (this.totalPrice === 0) {
        return `￥${this.minPrice}起送`
      } else if (this.totalPrice < this.minPrice) {
        let diff = this.minPrice - this.totalPrice
        return `还差￥${diff}起送`
      } else {
        return '去结算'
      }
    },
    payClass () {
      if (this.totalPrice < this.minPrice) {
        return 'no-enough'
      } else {
        return 'enough'
      }
    },
    listShow () {
      if (!this.totalCount) { // 购物车为空
        this.fold = true
        return false // 购物车列表不展开
      }
      let show = !this.fold // 购物车不为空，且此时为折叠状态，则展开
      if (show) {
        this.$nextTick(() => {
          if (!this.scroll) {
            this.scroll = new BScroll(this.$refs.listContent, {click: true})
          } else {
            this.scroll.refresh() // this.scroll已存在，不用重新实例化
          }
        })
      }
      return show
    }
  },
  methods: {
    drop (el) { // el为接收到的事件触发点，从cartcontrol经由goods传递而来
      for (let i = 0; i < this.balls.length; i++) {
        let ball = this.balls[i]
        if (!ball.show) {
          ball.show = true // 触发
          ball.el = el // 保存事件触发点
          this.dropBalls.push(ball)
          return
        }
      }
    },
    toggleList () {
      if (!this.totalCount) { // 购物车为空，不展开
        return
      }
      this.fold = !this.fold // 切换展开状态
    },
    empty () {
      this.selectFoods.forEach((food) => { // 遍历
        food.count = 0 // 将所有food.count置0
      })
    },
    hideList () {
      this.fold = true
    },
    pay () {
      if (this.totalPrice < this.minPrice) {
        return
      }
      window.alert(`支付${this.totalPrice}`)
    },
    increaseFood (target) {
      this.drop(target)
    },
    beforeDrop (el) { // el为小球
      let count = this.balls.length
      while (count--) {
        let ball = this.balls[count]
        if (ball.show) {
          let rect = ball.el.getBoundingClientRect() // 返回元素相对于视窗的位置
          let x = rect.left - 32 // 计算小球始末位置之间的距离--横向偏移
          let y = -(window.innerHeight - rect.top - 22) // 计算小球始末位置之间的距离--竖向偏移
          el.style.display = '' // 将元素显示出来
          el.style.webkitTransform = `translate3d(0, ${y}px, 0)` // 设置初始位置
          el.style.transform = `translate3d(0,${y}px,0)` // 外层元素做竖向动画
          let inner = el.getElementsByClassName('inner-hook')[0]
          inner.style.webkitTransform = `translate3d(${x}px,0,0)`
          inner.style.transform = `translate3d(${x}px,0,0)` // 内层元素做横向动画
        }
      }
    },
    dropping (el, done) {
      /* eslint-disable no-unused-vars */ // rf变量不使用
      let rf = el.offsetHeight // 手动触发浏览器重绘
      this.$nextTick(() => {
        el.style.webkitTransform = 'translate3d(0,0,0)'
        el.style.transform = 'translate3d(0,0,0)'
        let inner = el.getElementsByClassName('inner-hook')[0]
        inner.style.webkitTransform = 'translate3d(0,0,0)'
        inner.style.transform = 'translate3d(0,0,0)'
        el.addEventListener('transitionend', done)
      })
    },
    afterDrop (el) {
      let ball = this.dropBalls.shift() // 重置
      if (ball) {
        ball.show = false
        el.style.display = 'none'
      }
    }
  },
  components: {
    cartcontrol: cartcontrol
  }
}
</script>

<style lang="stylus" rel="stylesheet/stylus">

@import "../../common/stylus/mixin.styl"

.cart
  position: fixed
  left: 0
  bottom: 0
  width: 100%
  height: 48px
  z-index: 99
  .content
    display: flex
    background: #141d27
    color: rgba(255, 255, 255, .4)
    .content-left
      flex: 1
      font-size: 0
      .logo-wrapper
        display: inline-block
        vertical-align: top
        position: relative
        top: -10px
        margin: 0 12px
        padding: 6px
        width: 56px
        height: 56px
        box-sizing: border-box
        border-radius: 50%
        background: #141d27
        .logo
          width: 100%
          height: 100%
          border-radius: 50%
          text-align: center
          background: #2b343c
          &.on
            background: rgb(0, 160, 220)
          .icon-shopping_cart
            line-height: 44px
            font-size: 24px
            color: #80858a
            &.on
              color: #fff
        .num
          position: absolute
          top: 0
          right: 0
          width: 24px
          height: 16px
          line-height: 16px
          text-align: center
          border-radius: 16px
          font-size: 9px
          font-weight: 700
          color: #fff
          background: rgb(240, 20, 20)
          box-shadow: 0 4px 8px 0 rgba(0, 0, 0, .4)
      .price
        display: inline-block
        vertical-align: top
        margin-top: 12px
        padding-right: 12px
        line-height: 24px
        box-sizing: border-box
        border-right: 1px solid rgba(255, 255, 255, .1)
        font-size: 16px
        font-weight: 700
        &.on
          color: #fff
      .desc
        display: inline-block
        vertical-align: top
        margin: 12px 0 0 12px
        line-height: 24px
        font-size: 10px
    .content-right
      flex: 0 0 105px
      width: 105px
      .pay
        height: 48px
        line-height: 48px
        text-align: center
        font-size: 12px
        font-weight: 700
        background: #2b333b
        &.no-enough
          background: #2b333b
        &.enough
          color: #fff
          background: #00b43c
  .ball-container
    .ball
      position: fixed
      left: 32px
      bottom: 22px
      z-index: 199
      transition: all 0.4s cubic-bezier(0.49, -0.29, 0.75, 0.41) //抛物线
      .inner
        width: 16px
        height: 16px
        border-radius: 50%
        background: rgb(0, 160, 220)
        transition: all 0.4s linear
  .cart-list
    position: absolute
    left: 0
    top: 0
    width: 100%
    z-index: -1
    transform: translate3d(0, -100%, 0)
    &.fold-enter-active, &fold-leave-active
      transition: all .5s
    &.fold-enter, &.fold-leave-active
      transform: translate3d(0, 0, 0)
    .list-header
      height: 40px
      line-height: 40px
      padding: 0 18px
      background: #f3f5f7
      border-bottom: 1px solid rgba(7, 17, 27, .1)
      .title
        float: left
        font-size: 14px
        color: rgb(7, 17, 27)
      .empty
        float: right
        font-size: 12px
        color: rgb(0, 160, 220)
    .list-content
      padding: 0 18px
      max-height: 217px
      overflow: hidden
      background: #fff
      .food
        position: relative
        padding: 12px 0
        box-sizing: border-box
        border-1px(rgba(7, 17, 27, .1))
        .name
          line-height: 24px
          font-size: 14px
          color: rgb(7, 17, 27)
        .price
          position: absolute
          right: 90px
          bottom: 12px
          line-height: 24px
          font-size: 14px
          font-weight: 700px
          color: rgb(240, 20, 20)
        .cartcontrol-wrapper
          position: absolute
          right: 0
          bottom: 6px

.list-mask
  position: fixed
  top: 0
  left: 0
  width: 100%
  height: 100%
  z-index: 44
  backdrop-filter: blur(10px)
  opacity: 1
  background: rgba(7, 17, 17, .6)
  &.fade-enter-active, &.fade-leave-active
    transition: all .5s
  &.fade-enter, &.fade-leave-active
    opacity: 0
    background: rgba(7, 17, 27, 0)
</style>



