<template>
  <div class="cartcontrol">
    <transition name="move">
      <div class="cart-decrease" v-show="food.count > 0" @click.stop.prevent="decreaseFood">
        <span class="inner icon-remove_circle_outline"></span>
      </div>
    </transition>
    <div class="cart-count" v-show="food.count > 0">{{ food.count }}</div>
    <div class="cart-increase icon-add_circle" @click.stop.prevent="increaseFood"></div>
  </div>
</template>

<script>

import Vue from 'vue'

export default {
  name: 'cartcontrol',
  props: {
    food: {
      type: Object
    }
  },
  methods: {
    increaseFood (event) {
      if (!event._constructed) { // 屏蔽掉浏览器原生点击事件，_constructed是BetterScroll事件的属性，原生点击无此属性
        return
      }
      if (!this.food.count) {
        Vue.set(this.food, 'count', 1)
      } else {
        ++this.food.count
      }
      this.$emit('increase', event.target) // 派发事件
    },
    decreaseFood (event) {
      if (!event._constructed) { // 屏蔽掉浏览器原生点击事件，_constructed是BetterScroll事件的属性，原生点击无此属性
        return
      }
      if (this.food.count) {
        --this.food.count
      }
    }
  }
}
</script>

<style lang="stylus" rel="stylesheet/stylus">

@import "../../common/stylus/mixin"

.cartcontrol
  font-size: 0
  .cart-decrease
    display: inline-block
    padding: 6px
    opacity: 1
    transform: translate3d(0, 0, 0)
    &.move-enter-active, &.move-leave-active
      transition: all .4s linear
    &.move-enter, &.move-leave-active
      opacity: 0
      transform: translate3d(24px, 0, 0)
      .inner
       transform: rotate(180deg)
    .inner
      display: inline-block
      line-height: 24px
      font-size: 24px
      color: rgb(0, 160, 220)
      transition: all .4s linear
      transform: rotate(0)
  .cart-count
    display: inline-block
    vertical-align:top
    wdith: 12px
    padding-top: 6px
    line-height: 24px
    text-align: center
    color: rgb(147, 153, 159)
    font-size: 10px
  .cart-increase
    display: inline-block
    padding: 6px
    line-height: 24px
    font-size: 24px
    color: rgb(0, 160, 220)
</style>



