<template>
  <div class="ratingselect">
    <div class="rating-type">
      <span @click="select(2 ,$event)" class="block positive" :class="{'active': selectType===2}">{{ desc.all }}<span class="count">{{ ratings.length }}</span></span>
      <span @click="select(0 ,$event)" class="block positive" :class="{'active': selectType===0}">{{ desc.positive }}<span class="count">{{ positives.length }}</span></span>
      <span @click="select(1 ,$event)" class="block negative" :class="{'active': selectType===1}">{{ desc.negative }}<span class="count">{{ negatives.length }}</span></span>
    </div>
    <div @click="toggleContent" class="switch" :class="{'on': onlyContent}">
      <span class="icon-check_circle"></span>
      <span class="text">只看有内容的评价</span>
    </div>
  </div>
</template>

<script>

const POSITIVE = 0
const NEGATIVE = 1
const ALL = 2

export default {
  name: 'ratingselect',
  props: {
    ratings: {
      type: Array,
      default () {
        return []
      }
    },
    selectType: {
      type: Number,
      default: ALL
    },
    onlyContent: { // 只看有内容的评价 Flag
      type: Boolean,
      default: false
    },
    desc: {
      type: Object,
      default () {
        return {
          all: '全部',
          positive: '满意',
          negative: '不满意'
        }
      }
    }
  },
  computed: {
    positives () {
      return this.ratings.filter((rating) => { // 过滤
        return rating.rateType === POSITIVE
      })
    },
    negatives () {
      return this.ratings.filter((rating) => { // 过滤
        return rating.rateType === NEGATIVE
      })
    }
  },
  methods: {
    select (type, event) {
      if (!event._constructed) {
        return
      }
      // this.selectType = type // 不需要在子组件中修改，因为它会被传递给父组件，在父组件中修改，然后传回
      this.$emit('select', type) // 通过事件将数据传递给父组件
    },
    toggleContent () {
      if (!event._constructed) {
        return
      }
      // this.onlyContent = !this.onlyContent // 不需要在子组件中修改，因为它会被传递给父组件，在父组件中修改，然后传回
      this.$emit('toggle') // 通过事件将数据传递给父组件
    }
  },
  components: {

  }
}
</script>

<style lang="stylus" rel="stylesheet/stylus">

@import "../../common/stylus/mixin.styl"

.ratingselect
  .rating-type
    margin: 0 18px
    padding: 18px 0
    border-1px(rgba(7, 17, 27, .1))
    font-size: 0
    .block
      display: inline-block
      margin-right: 8px
      padding: 8px 12px
      line-height: 16px
      border-radius: 1px
      font-size: 12px
      color: rgb(77, 85, 93)
      &.active
        color: #fff
      .count
        margin-left: 2px
        font-size: 8px
      &.positive
        background: rgba(0, 160, 220, 0.2)
        &.active
          background: rgb(0, 160, 220)
      &.negative
        background: rgba(77, 85, 93, 0.2)
        &.active
          background: rgb(77, 85, 93)
  .switch
    padding: 12px 18px
    line-height: 24px
    border-bottom: 1px solid rgba(7, 17, 27, .1)
    color: rgb(147, 153, 159)
    font-size: 0
    .icon-check_circle
      display: inline-block
      vertical-align: top
      margin-right: 4px
      font-size: 24px
    &.on
      .icon-check_circle
        color: #00c850
    .text
      display: inline-block
      vertical-align: top
      font-size: 12px

</style>



